.card {
  background-size: cover;
  background-position: 50%;
  height: 250px;
  width: 250px;
  margin: 15px 25px;
  border: 5px solid #f1f1f1;
  border-radius: 2px;
  -webkit-box-shadow: 0 2px 4px rgba(58, 57, 57, 0.16),
    0 3px 6px rgba(58, 57, 57, 0.16);
  box-shadow: 0 2px 4px rgba(58, 57, 57, 0.16), 0 2px 4px rgba(58, 57, 57, 0.16);
  cursor: pointer;
}

.card .img-container {
  height: 100%;
  overflow: hidden;
  background: rgb(255, 255, 255);
}

.card > .img-container > img {
  height: 100%;
}

.card:hover {
  -webkit-box-shadow: 0 2px 4px rgba(58, 57, 57, 0.16), 0 10px 10px rgba(0, 0, 0, 0.22);
          box-shadow: 0 2px 4px rgba(58, 57, 57, 0.16), 0 10px 10px rgba(0, 0, 0, 0.22);
  -webkit-transform: scale(1.01);
      -ms-transform: scale(1.01);
          transform: scale(1.01);
  -webkit-transition: 0.1s;
  -o-transition: 0.1s;
  transition: 0.1s;
}

.card:active {
  -webkit-transform: translate(1px, 1px);
      -ms-transform: translate(1px, 1px);
          transform: translate(1px, 1px);
}



